.subcategory-items {
  display: none;
}

@media(max-width: 1199px) {
  .subcategory-items {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 15px;

  &__item {
    border: 1px solid #D118A3;
    color: #563494;
    font-weight: 500;
    border-radius: 10px;
    padding: 2px 10px;
    margin: 0 2.5px 5px 2.5px;
    transition: background-color 0.2s ease, color 0.2s ease;
    &:hover {
      color: #FF1AC6 !important;
    }
    &.active {
      color: #FF1AC6 !important;
    }
  }
}
}
